import React, { useState } from "react";
import { Card, Form } from "react-bootstrap";
import SentimentChart from "../../Analytics/layout/common/graph/sentimentChart";
import { reArrangeSeriesSentiment } from "../../../../../utils";
import { getsentimentLiveTrendMore } from "../../../../../actions/dashboardActions";
import { useDispatch } from "react-redux";
import moment from "moment";
import ImageSkeleton from "../../Analytics/layout/skeleton/imageSkeleton";
import { togglePlanSection_D } from "../../../../../actions/showDisplayModalAction";

const LiveTrend = (props) => {

    const { live_trend, loader, leaderBoard, nevigationRedirect, isUpgrade } = props

    const [activeTime, setActiveTime] = useState(0);
    const dispatch = useDispatch();

    const getSentimentData = (value) => {
        setActiveTime(value);
        dispatch(getsentimentLiveTrendMore(value));
    };

    const initialScore = live_trend?.initiallastScore?.length
        ? Number(live_trend.initiallastScore[0].sentiment)
        : null;

    if (initialScore) live_trend.data[0] = initialScore;

    const sentimentAnalytics = live_trend && Object.keys(live_trend).length
        ? reArrangeSeriesSentiment([live_trend], 1)
        : [];

    const hasData = live_trend?.initiallastScore?.length > 0;
    const hasLeaderboard = leaderBoard?.data?.length > 0;

    return (
        <div className={`mb-4 col-lg-${hasData || hasLeaderboard ? '5' : '5'}`}>
            <div className="created-walls-section h-100">
                <Card className="p-0 card-custom card-stretch">
                    <Card.Header className="p-3 mb-4">
                        <div className="m-0 d-flex align-items-center justify-content-between card-title h5 w-100">
                            <h6 className="card-label font-weight-bolder m-0">Live Sentiment  Trend</h6>
                            <div className="text-secondary font-size-sm"> {moment().format('hh:mm A MMMM D, YYYY')} </div>
                        </div>
                    </Card.Header>

                    {(hasLeaderboard || hasData || loader) && isUpgrade ? (
                        <>
                            <div className="date-filter date-filter-2">
                                <Form.Select value={activeTime} onChange={(e) => getSentimentData(e.target.value)} size="md" >
                                    <option value={1}>Last 24 Hours</option>
                                    <option value={2}>Last 48 Hours</option>
                                </Form.Select>
                            </div>

                            { loader ? ( <div className="home-sentiment h-100"><ImageSkeleton img={4} /></div> ) 
                            : ( <SentimentChart contentAnalyticsData={sentimentAnalytics} type="line" /> )
                            }
                        </>
                     ) : (

                        <div className="p-6 card-body">
                            <div className={`not_connected_err ${hasLeaderboard ? 'not_connected_err_new_2' : 'not_connected_err_new'} flex-center overlay-layer d-flex flex-column flex-center`}>
                                <p className="text-dark w-70 text-center font-size-h6">
                                     { !isUpgrade ? `Create a feed and instantly discover your audience's sentiments about your event in real time.` : 
                                     `Find out what your audience feels about the event with AI sentiment analysis.` }
                                </p>
                                <div className="not_conne_overlay d-flex flex-column flex-center">
                                    <a onClick={() => !isUpgrade ? dispatch(togglePlanSection_D(true)) : nevigationRedirect()} className="btn btn-primary" href="#" > { !isUpgrade ? 'Upgrade Now' : 'Add Feed' } </a>
                                </div>
                                
                            </div>
                        </div>
                    )}
                </Card>
            </div>
        </div>
    );
};

export default React.memo(LiveTrend);
